import { css } from "lit";

export default css`
  :host {
    display: inline-block;
  }

  :host(:focus) {
    outline: var(--exo-spacing-none);
  }

  /* Radio Wrapper */
  :host .radio {
    box-sizing: border-box;
    font-family: var(--exo-font-family);
    font-size: var(--exo-font-size-small);
    font-weight: var(--exo-font-weight-regular);
    line-height: var(--exo-line-height-denser);
    color: var(--exo-color-font);
    display: inline-flex;
    gap: var(--exo-spacing-x-small);
    cursor: pointer;
  }

  /* Radio Input Element */
  :host .radio__input {
    flex: 0 0 auto;
    align-self: flex-start;
    width: var(--exo-spacing-standard);
    height: var(--exo-spacing-standard);
    margin: var(--exo-spacing-3x-small);
    box-sizing: border-box;
    border: var(--exo-spacing-4x-small) solid
      var(--exo-color-background-deselected);
    border-radius: var(--exo-radius-circle);
    background-clip: content-box;
    background-color: var(--exo-color-background);
  }

  /* Radio Label Element */
  :host .radio__label {
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    display: flex;
    align-items: center;
  }

  /* Selected */
  :host .radio--checked .radio__input {
    border: var(--exo-spacing-4x-small) solid
      var(--exo-color-background-selected);
    background-color: var(--exo-color-background-selected);
    padding: var(--exo-spacing-3x-small);
  }

  /* Focus */
  :host .radio--focused .radio__input {
    border: var(--exo-spacing-4x-small) solid var(--exo-palette-gray-70);
    box-shadow: var(--exo-spacing-none) var(--exo-spacing-none)
      var(--exo-spacing-none)
      calc(var(--exo-spacing-3x-small) + var(--exo-spacing-4x-small))
      var(--exo-palette-aqua-30);
  }

  /* Selected Focus */
  :host .radio--checked.radio--focused .radio__input {
    border: var(--exo-spacing-4x-small) solid
      var(--exo-color-background-selected-hover);
  }

  /* Hover Unselected */
  :host .radio:hover:not(.radio--focused) .radio__input {
    border: var(--exo-spacing-4x-small) solid var(--exo-palette-gray-70);
  }

  /* Hover Selected */
  :host .radio:hover.radio--checked:not(.radio--focused) .radio__input {
    background-color: var(--exo-color-background-selected-hover);
    border: var(--exo-spacing-4x-small) solid
      var(--exo-color-background-selected-hover);
  }
`;
