import { customElement, property, state } from "lit/decorators.js";
import { html, LitElement } from "lit";
import dropdownClassNames from "./dropdown.constants";
import style from "./dropdown.styles";
import { ButtonFlavor, ButtonSize, ButtonType } from "../button/button";
import { IconVariant } from "../icon/icon";

const HIDE_EVENTS = ["resize", "scroll", "exo.closeDropdown"];
@customElement("ex-dropdown")
class Dropdown extends LitElement {
  static styles = style;

  @state()
  isOpen = false;

  @property() icon = "delete";

  @property() text = "";

  @property() flavor: ButtonFlavor = ButtonFlavor.BASE;

  @property() type: ButtonType = ButtonType.PRIMARY;

  @property({ type: Boolean }) disabled = false;

  @property() align: "left" | "right" = "left";

  @property() gap: number = 4;

  @property() size: ButtonSize = ButtonSize.DEFAULT;

  @property() label = "";

  onToggle() {
    if (this.disabled) {
      return;
    }

    if (!this.isOpen) {
      window.dispatchEvent(new CustomEvent("exo.closeDropdown"));
    }

    this.isOpen = !this.isOpen;
  }

  hide = () => {
    this.isOpen = false;
  };

  hideOnWindowClick = (event: MouseEvent) => {
    if (event.target !== this) {
      this.hide();
    }
  };

  hideOnEscape = (event: KeyboardEvent) => {
    if (event.key === "Escape") {
      this.hide();
    }
  };

  connectedCallback() {
    super.connectedCallback();
    HIDE_EVENTS.forEach((event) => {
      window.addEventListener(event, this.hide);
    });
    window.addEventListener("click", this.hideOnWindowClick);
    window.addEventListener("keyup", this.hideOnEscape);
  }

  disconnectedCallback() {
    HIDE_EVENTS.forEach((event) => {
      window.removeEventListener(event, this.hide);
    });
    window.removeEventListener("click", this.hideOnWindowClick);
    window.removeEventListener("keyup", this.hideOnEscape);
    super.disconnectedCallback();
  }

  adjustPosition = () => {
    const trigger = this.renderRoot.querySelector(
      `.${dropdownClassNames.trigger}`
    );
    const popover: HTMLDivElement | null = this.renderRoot.querySelector(
      `.${dropdownClassNames.popover}`
    );
    const triggerPosition = trigger?.getBoundingClientRect();
    const popoverPosition = popover?.getBoundingClientRect();
    const windowDimension = {
      height: window.innerHeight,
      width: window.innerWidth,
    };

    if (!triggerPosition || !popover) {
      return;
    }

    if (
      popoverPosition?.bottom &&
      popoverPosition?.bottom > windowDimension.height
    ) {
      popover.style.top = "auto";
      popover.style.bottom = `${
        windowDimension.height -
        triggerPosition.bottom +
        triggerPosition.height +
        this.gap
      }px`;
    }

    if (
      this.align === "left" &&
      popoverPosition?.right &&
      popoverPosition?.right > windowDimension.width
    ) {
      popover.style.left = `${
        popoverPosition.left - (popoverPosition.right - windowDimension.width)
      }px`;
    } else if (
      this.align === "right" &&
      popoverPosition?.left &&
      popoverPosition?.left < 0
    ) {
      popover.style.right = `${
        windowDimension.width - popoverPosition.right + popoverPosition.left
      }px`;
    }
  };

  updated() {
    const trigger = this.renderRoot.querySelector(
      `.${dropdownClassNames.trigger}`
    );
    const popover: HTMLDivElement | null = this.renderRoot.querySelector(
      `.${dropdownClassNames.popover}`
    );

    if (trigger && popover) {
      if (this.isOpen && !this.disabled) {
        const triggerPosition = trigger.getBoundingClientRect();

        if (this.align === "left") {
          popover.style.left = `${triggerPosition.left}px`;
        } else {
          popover.style.right = `${
            triggerPosition.right - triggerPosition.width
          }px`;
        }

        popover.style.top = `${
          triggerPosition.top + triggerPosition.height + this.gap
        }px`;
        popover.style.display = "block";
        requestAnimationFrame(this.adjustPosition);
      } else {
        popover.style.display = "none";
      }

      const dropdownHeight = popover?.querySelector("slot")?.offsetHeight;

      popover.style.height = `${dropdownHeight}px`;
    }
  }

  getTrigger() {
    if (this.text) {
      return html`
        <ex-button
          ?disabled=${this.disabled}
          flavor=${this.flavor}
          size="${this.size}"
          type="${this.type}"
          @click="${this.onToggle}"
        >
          ${this.icon
            ? html`<ex-icon
                icon="${this.icon}"
                slot="prefix"
                variant="${IconVariant.INVERSE}"
              ></ex-icon>`
            : ""}
          ${this.text}
          <ex-icon
            icon="${this.isOpen ? "caret-up" : "caret-down"}"
            slot="suffix"
            variant="${IconVariant.INVERSE}"
          ></ex-icon>
        </ex-button>
      `;
    }

    return html`
      <ex-icon-button
        icon="${this.icon}"
        ?disabled=${this.disabled}
        type="${this.type}"
        variant="${IconVariant.INVERSE}"
        @click="${this.onToggle}"
      >
      </ex-icon-button>
    `;
  }

  render() {
    return html`
      <div class="${dropdownClassNames.container}" aria-label="${this.label}">
        <div class="${dropdownClassNames.trigger}">${this.getTrigger()}</div>
        <div class=${dropdownClassNames.popover} aria-hidden="${!this.isOpen}">
          <slot></slot>
        </div>
      </div>
    `;
  }
}

export default Dropdown;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-dropdown": Dropdown;
  }
}
