import { customElement, property, query } from "lit/decorators.js";
import { classMap } from "lit/directives/class-map.js";
import { html, LitElement } from "lit";
import styles from "./button.styles";

export const enum ButtonType {
  PRIMARY = "primary",
  SECONDARY = "secondary",
  TERTIARY = "tertiary",
}

export const enum ButtonFlavor {
  BASE = "base",
  BRANDED = "branded",
  RISKY = "risky",
}

export const enum ButtonSize {
  DEFAULT = "default",
  LARGE = "large",
}
@customElement("ex-button")
class Button extends LitElement {
  static styles = styles;

  @query(".button")
  button: HTMLButtonElement;

  /**
   * flavor of selected button
   */
  @property() flavor: ButtonFlavor = ButtonFlavor.BASE;

  /**
   * Variants of buttons
   */
  @property() type: ButtonType = ButtonType.PRIMARY;

  /**
   * Size of the button
   */
  @property() size: ButtonSize = ButtonSize.DEFAULT;

  /**
   * disable the button
   */
  @property({ type: Boolean, reflect: true }) disabled = false;

  render() {
    return html`
      <button
        role="button"
        class=${classMap({
          button: true,
          "button--base": this.flavor === "base",
          "button--branded": this.flavor === "branded",
          "button--risky": this.flavor === "risky",
          "button--primary": this.type === "primary",
          "button--secondary": this.type === "secondary",
          "button--tertiary": this.type === "tertiary",
          "button--size-default": this.size === "default",
          "button--size-large": this.size === "large",
        })}
        aria-disabled=${this.disabled}
        tabindex=${this.disabled ? "-1" : "0"}
        ?disabled=${this.disabled}
        part="button"
      >
        <slot name="prefix"></slot>
        <slot></slot>
        <slot name="suffix"></slot>
      </button>
    `;
  }
}

export default Button;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-button": Button;
  }
}
