import { html, LitElement } from "lit";
import { property, customElement } from "lit/decorators.js";
import { classMap } from "lit/directives/class-map.js";
import styles from "./dialog.styles";

@customElement("ex-dialog")
class Dialog extends LitElement {
  static styles = styles;

  @property()
  title = "Default Dialog Title";

  @property()
  open = false;

  @property()
  staticBackdrop = false;

  @property()
  icon = "";

  constructor() {
    super();
    this.open = false;
    document.body.style.overflow = "hidden";
  }

  attributeChangedCallback(name: any, oldval: any, newval: any) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name === "open") {
      this.open = newval !== null;
    }
  }

  /**
   * close the modal
   */
  handleClose() {
    if (typeof this.staticBackdrop === "string" || this.staticBackdrop) {
      return;
    }
    this.close();
  }

  close() {
    this.open = false;
    this.dispatchEvent(new CustomEvent("onCancel"));
    document.body.style.overflow = "unset";
  }

  private keydownHandler(e: KeyboardEvent) {
    if (e.code === "Escape") {
      this.close();
    }
  }

  updated() {
    if (this.open) {
      document.body.style.overflow = "hidden";
      this.getRootNode().addEventListener("keydown", (e: Event) =>
        this.keydownHandler(e as KeyboardEvent)
      );
    } else {
      document.body.style.overflow = "unset";
      this.getRootNode().removeEventListener("keydown", (e: Event) =>
        this.keydownHandler(e as KeyboardEvent)
      );
    }
  }

  getDialogIcon() {
    return this.icon ? html`<ex-icon icon=${this.icon}></ex-icon>` : "";
  }

  render() {
    return html` <div
      class=${classMap({
        dialog: true,
        "dialog--backdrop": true,
        "dialog--opened": this.open,
        "dialog--closed": !this.open,
      })}
      aria-hidden="${!this.open}"
      @click=${() => this.handleClose()}
    >
      <div
        class="dialog--container"
        aria-label="dialog"
        aria-description="${this.title}"
        aria-modal="true"
        @click=${(e: Event) => e.stopPropagation()}
      >
        <div class="dialog--header">
          <h2 class="dialog--title">
            ${this.getDialogIcon()}
            <span>${this.title}</span>
          </h2>
          <ex-icon-button
            class="dialog--close"
            icon="cross"
            type="tertiary"
            @click=${this.close}
            circular
            aria-label="close"
            aria-description="closes the dialog"
          ></ex-icon-button>
        </div>
        <div class="dialog--body">
          <slot class="dialog--body-slot"></slot>
        </div>
        <div class="dialog--footer">
          <slot class="dialog--footer-slot" name="footer"></slot>
        </div>
      </div>
    </div>`;
  }
}

export default Dialog;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-dialog": Dialog;
  }
}
