import { customElement, property } from "lit/decorators.js";
import { classMap } from "lit/directives/class-map.js";
import { html, LitElement } from "lit";
import styles from "./menu.styles";

export const enum MenuWidth {
  DEFAULT = "default",
  MEDIUM = "medium",
  LARGE = "large",
  XLARGE = "x-large",
  XXLARGE = "xx-large",
}

@customElement("ex-menu")
class Menu extends LitElement {
  static styles = styles;

  /**
   * Width of the menu
   */
  @property() width: MenuWidth = MenuWidth.DEFAULT;

  render() {
    return html`
      <div
        role="menu"
        class=${classMap({
          menu: true,
          "menu--width-default": this.width === MenuWidth.DEFAULT,
          "menu--width-medium": this.width === MenuWidth.MEDIUM,
          "menu--width-large": this.width === MenuWidth.LARGE,
          "menu--width-x-large": this.width === MenuWidth.XLARGE,
          "menu--width-xx-large": this.width === MenuWidth.XXLARGE,
        })}
      >
        <slot class="menu--slot"></slot>
      </div>
    `;
  }
}

export default Menu;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-menu": Menu;
  }
}
