import { css } from "lit";

export default css`
  :host div {
    z-index: var(--exo-z-index-dialog);
    color: var(--exo-color-black);
  }

  :host .dialog--container {
    font-family: var(--exo-font-family);
    border-radius: var(--exo-radius-large);
    padding: var(--exo-spacing-x-large);
    box-shadow: 0 0 var(--exo-spacing-x-large) var(--exo-color-shadow-strong);
    min-width: var(--exo-size-6);
    background: var(--exo-color-background);
    opacity: var(--exo-opacity-0);
    transition: ease all var(--exo-time-standard);
    position: relative;
    max-width: 70%;
    transform: scale(0);
  }

  :host .dialog--header {
    margin-bottom: var(--exo-spacing-x-small);
    display: flex;
    justify-content: space-between;
  }

  :host .dialog--header ex-icon {
    display: inline-block;
    position: relative;
    left: calc(var(--exo-spacing-2x-small) * -1);
    top: calc(var(--exo-spacing-2x-small) - 1px);
  }

  :host .dialog--title {
    margin: var(--exo-spacing-none);
    font-size: var(--exo-font-size-4x-large);
    line-height: var(--exo-line-height-looser);
    font-weight: var(--exo-font-weigh-regular);
    color: var(--exo-color-gray-highest-contrast);
    width: calc(100% - (var(--exo-spacing-small) + var(--exo-spacing-x-large)));
  }

  :host .dialog--title span {
    vertical-align: top;
    display: inline-block;
    max-width: calc(100% - var(--exo-spacing-2x-large));
  }

  :host .dialog--body {
    font-weight: var(--exo-font-weight-regular);
    font-size: var(--exo-font-size-small);
    line-height: var(--exo-line-height-denser);
    max-height: 50vh;
    overflow: auto;
  }

  :host .dialog--footer {
    display: flex;
    flex-direction: row-reverse;
    margin-top: var(--exo-spacing-standard);
  }

  :host .dialog--backdrop {
    align-items: center;
    justify-content: center;
    position: fixed;
    inset: 0;
    opacity: var(--exo-opacity-0);
    z-index: var(--exo-z-index-dialog);
    transition: ease opacity var(--exo-time-slow);
    background-color: var(--exo-color-scrim);
    display: flex;
    visibility: hidden;
  }

  :host .dialog--backdrop.dialog--closed {
    animation: hideAfterDelay var(--exo-time-standard) forwards;
  }

  :host .dialog--backdrop.dialog--opened {
    display: flex;
    opacity: 1;
    visibility: unset;
  }

  :host .dialog--opened .dialog--container {
    opacity: 1;
    transform: scale(1);
  }

  :host .dialog--closed .dialog--container ex-button {
    pointer-events: none;
  }

  ::slotted(*) {
    font-weight: var(--exo-font-weight-light);
    margin-left: var(--exo-spacing-small);
  }

  @-webkit-keyframes hideAfterDelay {
    0% {
      display: flex;
      opacity: 1;
    }
    100% {
      display: none;
      opacity: 0;
    }
  }
  @keyframes hideAfterDelay {
    0% {
      display: flex;
      opacity: 1;
    }

    100% {
      visibility: hidden;
      display: none;
      opacity: 0;
    }
  }

  @media only screen and (max-width: 480px) {
    :host .dialog--container {
      min-width: 70vw;
    }
  }
`;
