import { customElement, property } from "lit/decorators.js";
import { classMap } from "lit/directives/class-map.js";
import { html, LitElement } from "lit";
import styles from "./icon-button.styles";
import { IconVariant } from "../icon/icon";

export const enum IconButtonType {
  PRIMARY = "primary",
  SECONDARY = "secondary",
  TERTIARY = "tertiary",
}

@customElement("ex-icon-button")
class IconButton extends LitElement {
  static styles = styles;

  /**
   * Variant or type of the button
   */
  @property() type: IconButtonType = IconButtonType.PRIMARY;

  /**
   * Icon associated with the button
   */
  @property()
  icon = "save";

  /**
   * disable the button
   */
  @property({ type: Boolean }) disabled = false;

  /**
   * Circular button
   */
  @property({ type: Boolean }) circular = false;

  /**
   * Icon variants
   */
  @property()
  variant: IconVariant = IconVariant.DEFAULT;

  render() {
    return html`
      <button
        role="button"
        class=${classMap({
          "icon-button": true,
          "icon-button--primary": this.type === "primary",
          "icon-button--secondary": this.type === "secondary",
          "icon-button--tertiary": this.type === "tertiary",
          "icon-button--circular": this.circular,
        })}
        aria-disabled=${this.disabled}
        ?disabled=${this.disabled}
      >
        <ex-icon
          icon="${this.icon}"
          type="${this.type}"
          variant=${this.variant}
        ></ex-icon>
      </button>
    `;
  }
}

export default IconButton;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-button-icon": IconButton;
  }
}
