import { customElement, query } from "lit/decorators.js";
import { classMap } from "lit/directives/class-map.js";
import { html, LitElement } from "lit";
import styles from "./menu-item.styles";
import SlotController from "../../internal/slot";

@customElement("ex-menu-item")
class MenuItem extends LitElement {
  static styles = styles;

  @query(".button")
  button: HTMLButtonElement;

  private readonly slotController = new SlotController(
    this,
    "menu-item-header"
  );

  render() {
    const headerClass = {
      "menu-item--header": true,
      hide: !this.slotController.test("menu-item-header"),
    };

    return html`
      <button class="menu-item" role="menuitem">
        <div class=${classMap(headerClass)}>
          <slot name="menu-item-header"></slot>
        </div>

        <div class="menu-item--body">
          <slot class="menu-item--body-slot"></slot>
        </div>
      </button>
    `;
  }
}

export default MenuItem;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-menu-item": MenuItem;
  }
}
