import { customElement, property } from "lit/decorators.js";
import { html, LitElement } from "lit";
import { classMap } from "lit/directives/class-map.js";
import styles from "./toggle.styles";
import propertyConverter from "../helpers";

@customElement("ex-toggle")
class Toggle extends LitElement {
  static styles = styles;

  @property({ type: Boolean, converter: propertyConverter.boolean }) on = false;

  @property({ type: Boolean, converter: propertyConverter.boolean }) disabled =
    false;

  /*
   * Size of the toggle - small, large
   */
  @property() size = "small";

  @property() leftIcon = "cross";

  @property() rightIcon = "tick";

  @property() label = "toggle";

  @property() tabndex = 1;

  constructor() {
    super();
    this.addEventListener("keyup", this.onKeyUp);
  }

  onClick() {
    if (!this.disabled) {
      this.on = !this.on;
      this.dispatchEvent(
        new CustomEvent("onChange", {
          detail: !this.on,
        })
      );
    }
  }

  onKeyUp(e: KeyboardEvent) {
    if (!this.disabled && e.key === "Enter") {
      this.on = !this.on;
      this.dispatchEvent(
        new CustomEvent("onChange", {
          detail: !this.on,
        })
      );
    }
  }

  render() {
    const classList = {
      toggle: true,
      "toggle--container": true,
      on: this.on,
      disabled: this.disabled,
      [this.size]: true,
    };

    return html`
      <div
        class="${classMap(classList)}"
        aria-disabled=${this.disabled}
        aria-label=${this.label}
        aria-checked=${this.on}
        role="switch"
        @keyup=${this.onClick}
      >
        <input type="checkbox" aria-checked=${this.on} tabindex="-1" />
        <div class="toggle--indicator-lane" @click=${this.onClick}>
          <ex-icon
            icon="${this.leftIcon}"
            variant="${this.disabled ? "disabled" : "inverse"}"
          ></ex-icon>
          <div class="toggle--indicator"></div>
          <ex-icon
            icon="${this.rightIcon}"
            variant="${this.disabled ? "disabled" : "inverse"}"
          ></ex-icon>
        </div>
      </div>
    `;
  }
}

export default Toggle;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-toggle": Toggle;
  }
}
