// Import all the svg's from the current folder
const SVG = import.meta.globEager("./assets/*/*.svg", {
  assert: { type: "raw" },
}) as unknown as Record<string, string>;

const Icons: Record<string, string> = {};

export const getIconNameFromPath = (str: string): string => {
  const file = str.split("/").pop() || str;
  return file.substring(0, file.lastIndexOf("."));
};

Object.entries(SVG).forEach(([key, value]) => {
  const svgName = getIconNameFromPath(key);

  if (svgName.length > 1) {
    Icons[svgName] = value;
  }
});

export const IconOptions = Object.keys(Icons);

export default Icons;
