import { customElement, property } from "lit/decorators.js";
import { html, LitElement } from "lit";
import { classMap } from "lit/directives/class-map.js";
import styles from "./tile.styles";
import SlotController from "../../internal/slot";

@customElement("ex-tile")
class Tile extends LitElement {
  static styles = styles;

  /**
   * Hyperlink of the tile
   */
  @property() href = "";

  /**
   * Target of the tile
   */
  @property() target: "_blank" | "_new" | "_parent" | "_self" | "_top";

  /**
   * Title of the tile
   */
  @property() title = "This is tile component";

  /**
   * aria-label of the tile
   */
  @property() label = "Label of the tile";

  /**
   * aria-describedby of the tile
   */
  @property() describedby = "Description of the tile";

  /**
   * border of the tile
   */
  @property({ type: Boolean }) borderless = false;

  private readonly slotController = new SlotController(
    this,
    "footer",
    "header"
  );

  render() {
    const headerClass = {
      "tile--header": true,
      hide: !this.slotController.test("header"),
    };

    const footerClass = {
      "tile--footer": true,
      hide: !this.slotController.test("footer"),
    };

    return html`
      <a
        class="tile"
        href=${this.href}
        target=${this.target}
        rel="noreferrer noopener"
        title=${this.title}
        aria-label=${this.label}
        aria-describedby=${this.describedby}
        ?borderless=${this.borderless}
      >
        <div class=${classMap(headerClass)}>
          <slot name="header"></slot>
        </div>

        <div class="tile--body">
          <slot></slot>
        </div>

        <div class=${classMap(footerClass)}>
          <slot name="footer"></slot>
        </div>
      </a>
    `;
  }
}

export default Tile;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-tile": Tile;
  }
}
