import { css } from "lit";

export default css`
  :host .menu-item {
    position: relative;
    padding: var(--exo-spacing-standard) var(--exo-spacing-large);
    box-sizing: border-box;
    cursor: pointer;
    transition: background-color var(--exo-time-standard) ease,
      border-color var(--exo-time-standard) ease;
    border: var(--exo-spacing-4x-small) solid var(--exo-color-background);
    background-color: var(--exo-color-background);
    font: var(--exo-doc-body-small);
    color: var(--exo-doc-body-small-color);
    outline: none;
    text-align: left;
    width: 100%;
    display: block;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  :host .menu-item:hover {
    background-color: var(--exo-color-background-action-secondary-hover);
    border-color: var(--exo-color-background-action-secondary-hover);
  }

  :host .menu-item:focus,
  :host .menu-item:focus-visible {
    background-color: var(--exo-color-background-action-secondary-hover);
    border-color: var(--exo-color-border-selected);
  }

  :host .menu-item .menu-item--header {
    margin-bottom: var(--exo-spacing-x-small);
    font: var(--exo-text-label-standard-bold);
  }

  :host .hide {
    display: none;
  }
`;
