import { customElement, property } from "lit/decorators.js";
import { html, LitElement } from "lit";
import {
  bodyCss,
  headCss,
  subheaderCss,
  tableCss,
  tdCss,
  tdGroupCss,
  thCss,
  trCss,
} from "./table.styles";

@customElement("ex-table")
class Table extends LitElement {
  static styles = tableCss;

  /* eslint-disable-next-line class-methods-use-this */
  render() {
    return html`
      <div class="table">
        <slot></slot>
      </div>
    `;
  }
}

@customElement("ex-table-head")
class THead extends LitElement {
  static styles = headCss;

  /* eslint-disable-next-line class-methods-use-this */
  render() {
    return html`
      <div class="table-header">
        <slot></slot>
      </div>
    `;
  }
}

@customElement("ex-table-body")
class TBody extends LitElement {
  static styles = bodyCss;

  @property() height = "";

  render() {
    return html`
      <style>
        :host .table-body {
          height: ${this.height ? this.height : "auto"};
          overflow: ${this.height ? "scroll" : "auto"};
        }
      </style>
      <div class="table-body">
        <slot></slot>
      </div>
    `;
  }
}

@customElement("ex-table-tr")
class Tr extends LitElement {
  static styles = trCss;

  @property({ type: Boolean }) clickable: boolean;

  /* eslint-disable-next-line class-methods-use-this */
  render() {
    return html`
      <style>
        :host {
          cursor: ${this.clickable ? "pointer" : "default"};
        }
      </style>
      <div class="table-row">
        <slot></slot>
      </div>
    `;
  }
}

@customElement("ex-table-td")
class Td extends LitElement {
  static styles = tdCss;

  /* eslint-disable-next-line class-methods-use-this */
  render() {
    return html`
      <div class="table-cell">
        <slot></slot>
      </div>
    `;
  }
}

@customElement("ex-table-th")
class Th extends LitElement {
  static styles = thCss;

  /* eslint-disable-next-line class-methods-use-this */
  render() {
    return html`
      <div class="table-header-cell" scope="col">
        <slot></slot>
      </div>
    `;
  }
}

@customElement("ex-table-td-group")
class TdGroup extends LitElement {
  static styles = tdGroupCss;

  /* eslint-disable-next-line class-methods-use-this */
  render() {
    return html`
      <div class="table-cell-group">
        <slot></slot>
      </div>
    `;
  }
}

@customElement("ex-table-subheader")
class TSubheader extends LitElement {
  static styles = subheaderCss;

  /* eslint-disable-next-line class-methods-use-this */
  render() {
    return html`
      <div class="table-sub-header">
        <slot></slot>
      </div>
    `;
  }
}

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-table": Table;
    "ex-table-head": THead;
    "ex-table-body": TBody;
    "ex-table-tr": Tr;
    "ex-table-td": Td;
    "ex-table-th": Th;
    "ex-table-td-group": TdGroup;
    "ex-table-subheader": TSubheader;
  }
}

export { Table, THead, TBody, Tr, Td, Th, TdGroup, TSubheader };
