import { css } from "lit";

export default css`
  :host {
    --exo-width-small: 40px;
    --exo-height-small: 20px;
    --exo-width-large: 48px;
    --exo-height-large: 24px;
  }

  :host(:focus-visible) {
    outline: none;
  }

  :host .toggle--container {
    padding: var(--exo-spacing-3x-small);
    border-radius: var(--exo-radius-x-large);
    border: var(--exo-spacing-4x-small) solid transparent;
    transition: border var(--exo-time-standard) ease;
  }

  :host .toggle--container input {
    position: absolute;
    opacity: var(--exo-opacity-0);
  }

  :host .toggle--indicator-lane {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: var(--exo-radius-x-large);
    padding: var(--exo-spacing-2x-small);
    gap: var(--exo-spacing-x-small);
    background: var(--exo-color-background-deselected);
    justify-content: space-around;
    position: relative;
    transition: background var(--exo-time-standard) ease,
      color var(--exo-time-standard) ease;
    cursor: pointer;
    font-size: var(--exo-font-size-micro);
    color: var(--exo-color-background);
    box-sizing: border-box;
  }

  :host .toggle--indicator {
    background: var(--exo-color-background);
    flex-grow: var(--exo-spacing-none);
    border-radius: var(--exo-radius-x-large);
    position: absolute;
    left: 12%;
    transition: left var(--exo-time-standard) ease,
      background var(--exo-time-standard) ease;
    z-index: 1;
  }

  :host .on .toggle--indicator-lane {
    background: var(--exo-color-background-selected);
  }

  :host .toggle--indicator-lane ex-icon::part(icon) {
    width: 1em;
    height: 1em;
  }

  /**** hover ****/

  :host(:hover) .toggle--indicator-lane {
    background: var(--exo-color-background-deselected-hover);
  }

  :host(:hover) .on .toggle--indicator-lane {
    background: var(--exo-color-background-selected-hover);
  }

  /**** Focus ****/

  :host(:focus) .toggle--container:not(.disabled) {
    border: var(--exo-spacing-4x-small) solid
      var(--exo-color-background-selected);
  }

  /**** Disabled ****/

  :host .disabled .toggle--indicator-lane,
  :host .disabled.on .toggle--indicator-lane {
    cursor: default;
    background: var(--exo-color-background-disabled);
  }

  :host .disabled .toggle--indicator {
    background: var(--exo-color-background-deselected);
  }

  :host .disabled ex-icon {
    color: var(--exo-color-background-deselected);
  }

  /**** Size ****/

  :host .small .toggle--indicator-lane {
    width: var(--exo-width-small);
    height: var(--exo-height-small);
  }

  :host .small .toggle--indicator {
    width: var(--exo-spacing-small);
    height: var(--exo-spacing-small);
  }

  :host .small .toggle--indicator-lane {
    font-size: var(--exo-font-size-micro);
  }

  :host .small.on .toggle--indicator {
    left: 58%;
  }

  :host .large .toggle--indicator-lane {
    width: var(--exo-width-large);
    height: var(--exo-height-large);
  }

  :host .large .toggle--indicator {
    width: var(--exo-spacing-standard);
    height: var(--exo-spacing-standard);
  }

  :host .large .toggle--indicator-lane {
    font-size: var(--exo-font-size-medium);
  }

  :host .large:not(.on) .toggle--indicator {
    left: 11%;
  }

  :host .large.on .toggle--indicator {
    left: 57%;
  }
`;
