import { customElement } from "lit/decorators.js";
import { html, LitElement } from "lit";
import { classMap } from "lit/directives/class-map.js";
import styles from "./card.styles";
import SlotController from "../../internal/slot";

@customElement("ex-card")
class Card extends LitElement {
  static styles = styles;

  private readonly slotController = new SlotController(
    this,
    "footer",
    "header"
  );

  render() {
    const headerClass = {
      "card--header": true,
      hide: !this.slotController.test("header"),
    };

    const footerClass = {
      "card--footer": true,
      hide: !this.slotController.test("footer"),
    };

    // @ts-ignore
    return html`
      <div class="card">
        <div class=${classMap(headerClass)}>
          <slot name="header"></slot>
        </div>

        <div class="card--body">
          <slot></slot>
        </div>

        <div class=${classMap(footerClass)}>
          <slot name="footer"></slot>
        </div>
      </div>
    `;
  }
}

export default Card;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-card": Card;
  }
}
