import { css } from "lit";

export default css`
  /** LIGHT MODE */

  :host .loader--container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    transition: opacity var(--exo-time-slow);
    position: absolute;
    inset: 0;
  }

  :host .loader--container.loader--backdrop {
    opacity: var(--exo-opacity-0);
    z-index: var(--exo-z-index-dialog);
    background-color: var(--exo-color-scrim);
  }

  :host .loader--container.loader--inline {
    width: auto;
    height: auto;
    position: relative;
  }

  :host .loader {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    flex-direction: column;
    padding: var(--exo-spacing-x-small);
  }

  :host .loader #ex-loader {
    width: var(--exo-spacing-x-large);
    height: var(--exo-spacing-x-large);
  }

  :host .loader.loader--medium #ex-loader {
    width: var(--exo-spacing-2x-large);
    height: var(--exo-spacing-2x-large);
  }

  :host .loader.loader--large #ex-loader {
    width: var(--exo-spacing-3x-large);
    height: var(--exo-spacing-3x-large);
  }

  :host .loader .loader--body {
    margin: var(--exo-spacing-x-small);
    display: block;
  }
`;
