import { customElement, property } from "lit/decorators.js";
import { html, LitElement, PropertyValues } from "lit";
import { classMap } from "lit/directives/class-map.js";
import type Radio from "../radio/radio";
import styles from "./radio-group.styles";

const KEYS = {
  ArrowUp: "ArrowUp",
  ArrowDown: "ArrowDown",
  ArrowLeft: "ArrowLeft",
  ArrowRight: "ArrowRight",
  Space: "Space",
};

const ARROW_UP_AND_LEFT_KEYS = [KEYS.ArrowUp, KEYS.ArrowLeft];
const ARROWS_AND_SPACE_KEYS = Object.values(KEYS);

@customElement("ex-radio-group")
class RadioGroup extends LitElement {
  static styles = styles;

  /**
   * Label for the Radio Group
   */
  @property() label: string;

  /**
   * Selected value of the Radio Group
   */
  @property({ reflect: true }) value = "";

  /**
   * Name assigned to the Radio
   */
  @property() name = "option";

  /**
   * Shows the fieldset and legend of the Radio Group
   */
  @property({ type: Boolean, attribute: "fieldset", reflect: true }) fieldset =
    false;

  private getAllRadios() {
    return Array.from(this.querySelectorAll<Radio>("ex-radio"));
  }

  connectedCallback() {
    super.connectedCallback();
    this.updateCheckedRadio();
  }

  private updateCheckedRadio() {
    const radios = this.getAllRadios();
    radios.forEach((radio) => {
      radio.checked = radio.value === this.value;
    });
  }

  private handleRadioClick(event: MouseEvent) {
    const target = event.target as Radio;
    if (target.value) {
      this.value = target.value;
      this.updateCheckedRadio();
    }
  }

  static findIncrement(event: KeyboardEvent) {
    if (event.code === KEYS.Space) {
      return 0;
    }
    if (ARROW_UP_AND_LEFT_KEYS.includes(event.code)) {
      return -1;
    }
    return 1;
  }

  private handleKeyDown(event: KeyboardEvent) {
    if (!ARROWS_AND_SPACE_KEYS.includes(event.code)) {
      return;
    }
    const radios = this.getAllRadios();
    const checkedRadio = radios.find((radio) => radio.checked) ?? radios[0];
    let index = radios.indexOf(checkedRadio) + RadioGroup.findIncrement(event);
    if (index < 0) {
      index = radios.length - 1;
    } else if (index > radios.length - 1) {
      index = 0;
    }
    this.getAllRadios().forEach((radio) => {
      radio.checked = false;
      radio.tabIndex = -1;
    });

    this.value = radios[index].value;
    radios[index].checked = true;
    radios[index].tabIndex = 0;
    if (typeof radios[index].focus === "function") {
      radios[index].focus();
    }

    event.preventDefault();
  }

  private handleSlotChange() {
    const radios = this.getAllRadios();

    radios.forEach((radio) => {
      radio.checked = radio.value === this.value;
    });

    if (!radios.some((radio) => radio.checked)) {
      radios[0].tabIndex = 0;
    }
  }

  update(changedProps: PropertyValues<this>) {
    super.update(changedProps);
    if (
      changedProps.has("value") &&
      changedProps.get("value") !== this.value &&
      this.hasUpdated
    ) {
      const event = new CustomEvent("on-change", {
        bubbles: true,
        cancelable: false,
        composed: true,
        detail: {
          value: this.value,
        },
      });
      this.dispatchEvent(event);
    }
  }

  render() {
    return html`
      <fieldset
        role="radiogroup"
        class=${classMap({
          "radio-group": true,
          "radio-group--has-fieldset": this.fieldset,
        })}
      >
        <legend class="radio-group__label">
          <slot name="label">${this.label}</slot>
        </legend>
        <slot
          class="radio-group__slot"
          @click=${this.handleRadioClick}
          @keydown=${this.handleKeyDown}
          @slotchange=${this.handleSlotChange}
        >
        </slot>
      </fieldset>
    `;
  }
}

export default RadioGroup;

declare global {
  /* eslint-disable-next-line no-unused-vars */
  interface HTMLElementTagNameMap {
    "ex-radio-group": RadioGroup;
  }
}
