import { css } from "lit";

export default css`
  :host .tile {
    font-family: var(--exo-font-family);
    color: var(--exo-color-font);
    background-color: var(--exo-color-background);
    border: var(--exo-spacing-4x-small) solid var(--exo-color-border);
    box-shadow: 0 var(--exo-spacing-3x-small) var(--exo-spacing-2x-small)
      var(--exo-color-shadow-moderate);
    font-size: var(--exo-font-size-small);
    line-height: var(--exo-line-height-body);
    width: 100%;
    overflow: hidden;
    border-radius: var(--exo-radius-large);
    transition: background-color var(--exo-time-standard) ease;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    box-sizing: border-box;
    position: relative;
    display: block;
    text-decoration: none;
  }

  :host .tile:focus-visible {
    outline: unset;
  }

  :host .tile:not([borderless]):hover {
    border: var(--exo-spacing-4x-small) solid var(--exo-color-border-tertiary);
  }

  :host .tile:not([borderless]):active,
  :host .tile:not([borderless]):focus {
    border: var(--exo-spacing-4x-small) solid var(--exo-color-border-tertiary);
    outline: var(--exo-spacing-3x-small) solid var(--exo-color-outline-weak);
    outline-offset: 0;
  }

  :host [borderless].tile {
    transition: all var(--exo-time-standard) ease;
    box-shadow: none;
    border: var(--exo-spacing-3x-small) solid transparent;
  }

  :host [borderless].tile:hover {
    box-shadow: 0 0 var(--exo-spacing-2x-small) var(--exo-color-shadow-moderate);
    opacity: var(--exo-opacity-6);
  }

  :host [borderless].tile:active,
  :host [borderless].tile:focus {
    border: var(--exo-spacing-3x-small) solid var(--exo-color-background-brand);
    box-shadow: none;
    opacity: unset;
  }

  :host .tile .tile--body,
  :host .tile .tile--footer {
    padding: var(--exo-spacing-standard) var(--exo-spacing-medium);
  }

  :host .hide {
    display: none;
  }
`;
